:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.search-form {
  z-index: 10;
  width: 100%;
  text-align: center;
  background-color: #8833f8;
  padding-top: 10px;
  padding-bottom: 10px;
  position: fixed;
}

.info {
  justify-content: space-evenly;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.gallery {
  max-width: 1400px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: grid;
  position: relative;
  top: 30px;
}

.gallery__image {
  height: 70%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.photo-card {
  position: relative;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.photo-card:hover {
  transform: scale(1.03);
}

.load-more {
  color: #fff;
  background-color: #8833f8;
  margin: 70px auto 20px;
  padding: 5px;
  display: none;
}

.is-hidden {
  display: block;
}

